body {
  font-family: 'Courier New';
  margin: 0;
  color: white;
}

.world {
  background: #000;
}

/* ======================= HELLO ======================== */

.hello {
	max-width: 1024px;
	display: block;
	margin: 0 auto;
	padding: 0 15px;
}

.hello img {	
	width: 100%;
	margin-top: 50px;
	animation: floatingShadow 10s ease-in-out infinite;
}

.hello h1 {
	color: yellow;
  	font-family: 'Press Start 2P', cursive;
	margin: 70px 0 100px 0;
	font-size: 3em;
	z-index: 999;
}

.hello input {
	padding: 10px 10px;
	width: 400px;
	font-size: 1em;
	font-weight: bold;
	border: 2px solid white;
	letter-spacing: -1px;
  	font-family: 'Courier New', cursive;
	text-transform: capitalize;
}

