.illussion::after {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(18, 16, 16, 0.1);
  opacity: 0;
  z-index: 2;
  pointer-events: none;
  animation: flicker 0.15s infinite;
}

.illussion::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06));
  z-index: 2;
  background-size: 100% 2px, 3px 100%;
  pointer-events: none;
}

.illussion {
  animation: textShadow 1.6s infinite;
}

/* ======================= TIME ======================== */

.time {
  width: 100%;
  position: fixed;
  left: -115px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: sunrise 50s infinite linear;
}

.time .eclipse {
  border-right: 2px solid black;
  border-left: 2px solid black;
  position: relative;
  max-width: 1500px;
  width: 200%;
  height: 100%;
}

.time .moon, .time .sun {
  position: absolute;
  top: 0;
  width: 200%;
  max-width: 1500px;
  border-radius: 100%;
}

.sun {
  background: #fff;
}

.moon {
  background: #ddd;
  animation: time 100s infinite linear, sunrise 100s infinite linear;
}

.clock {
  position: fixed;
  z-index: 999;
  bottom: 25px;
  right: 5%;
  font-size: 1.2em;
  background: black;
  padding: 10px;
  font-family: 'Press Start 2P', cursive;
}

.clock svg {
	margin: 0 0 -4px 0;
    font-size: 30px;
}
	
@keyframes sunrise {
  0% {
    background: #fff;
  }
  25% {
  	background: #eee;
  }
  50% {
    background: #ddd;
  }
  75% {
  	background: #eee;
  }
  100% {
    background: #fff;
  }
}

@keyframes time {
  0% {
    left: 1500px;
  }
  100% {
    left: -1500px;
  }
}

/* ========================= DIAMETER =========================== */

.diameter {
	z-index: 2;
	width: 1px;
	height: 100%;
	background: black;
	position: fixed;
	top: 0;
	left: 50%;
}

/* ========================= DAYRRE =========================== */

.dayere h1 {
	z-index: 2;
	text-transform: uppercase;
  	font-family: 'Press Start 2P', cursive;
	position: fixed;
	max-width: 36%;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
	font-size: 1.2em;
	top: 25px;
	left: 5%;
}

/* ========================= POLAR =========================== */

.polar {
	position: fixed;
	left: 5%;
	background: black;
	width: 100px;
	height: 100px;
	bottom: 0;
}

.polar .central {
	position: relative;
	width: 50%;
	height: 50%;
	margin-top: 25%;
	margin-left: 25%;
	background: red;
}

/* ========================= INDIVIDUAL =========================== */

.individual {
	position: fixed;
	right: 5%;
	top: 35px;
	width: 150px;
	text-align: right;
	text-overflow: ellipsis;
	overflow: hidden;
	text-transform: uppercase;
  	font-family: 'Courier New';
  	color: black;
  	font-size: 1.2em;
  	text-decoration: underline;
  	z-index: 2;
}

/* ========================= MESSAGES =========================== */

.messages {
	z-index: 3;	
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	text-align: center;
  	padding: 60px 0;
  	overflow: scroll;
}

.messages .message {
	height: 100px;
	position: relative;
	color: black;
	font-size: 0.75em;
}

.messages .message:last-child {
	margin-bottom: 75px;
}

.messages .message:first-child {
	margin-top: 25px;
}

.messages .message .sphere {
	cursor: pointer;
	display: block;
	position: absolute;
	left: calc(50% - 25px);
	z-index: 999;
	margin: 0 auto;
	width: 50px;
	height: 50px;
	background: black;
	border-radius: 100%;
	-webkit-border-radius: 100%;
}

.messages .message span {
    padding-top: 50px;
    position: relative;
    width: 100%;
    float: left;
}

.messages .orbits div {
	position: absolute;
	background: none;
	border-radius: 100%;
	-webkit-border-radius: 100%;
	border: 1px solid #aaa;
}

.messages .orbits .orbit-1{
	top: -30px;
	left: calc(50% - 60px);
	width: 120px;
	height: 120px;
}

.messages .orbits .orbit-2{
	top: -60px;
	left: calc(50% - 90px);
	width: 180px;
	height: 180px;
}

.messages .message .silent svg {
	color: white;
	font-size: 1.25em;
    padding-top: 17px;
    position: relative;
}

@media screen only (max-width: 768px) {
	.messages .message svg {
 	   padding-top: 15px;
	}
}

.wave {
  animation: moveTheWave 2400ms linear infinite;
  stroke-dasharray: 0 16 101 16;
}

@keyframes moveTheWave {

  0% {
    stroke-dashoffset: 0;
    transform: translate3d(0, 0, 0);
  }

  100% {
    stroke-dashoffset: -133;
    transform: translate3d(-90px, 0, 0);
  }

}
